import React, { Component } from "react";
import { BrowserRouter, Route } from "react-router-dom";

import Header from "../Header";
import Content from "../Content";
import Footer from "../Footer";

import database from "../../data/database.js";

import "./style.min.css";
import "./media320-414.min.css";

class MainPage extends Component {
  state = {
    langs: database.langs,
    pages: database.pages,
    ClockInterval: 0,
    scrollActivated: false,
  };

  componentDidMount() {
    this.ClockInterval = 0;
    this.HandleClickBurgerOpenNav();
    this.StartCircularActions();
  }

  render() {
    return (
      <div className="body">
        <div className="inner__body">
          <Header langs={this.state.langs} />
          <Content
            pages={this.state.pages}
            lang={this.state.langs[0].active == 1 ? 0 : 1}
          />
          <Footer lang={this.state.langs[0].active == 1 ? 0 : 1} />
        </div>
      </div>
    );
  }

  HandleClickBurgerOpenNav() {
    let NavBurger = document.querySelectorAll(".SmallDeviceBurger");
    let navigateBar = document.querySelector(".navigate");
    NavBurger.forEach((Burger) => {
      Burger.addEventListener("click", () => {
        navigateBar.dataset.isopen = this.checkBool(navigateBar.dataset.isopen);
        NavBurger.forEach((Burger) => {
          Burger.dataset.isopen = this.checkBool(Burger.dataset.isopen);
        });
      });
    });
  }

  checkBool(elem) {
    switch (elem) {
      case "false":
        return "true";
        break;
      default:
        return "false";
    }
  }

  checkOffset() {
    const allItems = document.querySelectorAll(".navigate-item");
    const allPages = document.querySelectorAll(".page");
    this.toUp();
    allPages.forEach((Page) => {
      if (
        window.pageYOffset >= Page.offsetTop - 60 &&
        window.pageYOffset <= Page.offsetTop + Page.offsetHeight
      ) {
        allItems.forEach((Item) => {
          if (Item.dataset.pageId == Page.dataset.pageId) {
            Item.dataset.active = 1;
            Page.dataset.active = 1;
          } else {
            Item.dataset.active = 0;
            Page.dataset.active = 0;
          }
        });
      }
    });
  }

  checkPosToScroll() {
    const allItems = document.querySelectorAll(".navigate-item");
    const allPages = document.querySelectorAll(".page");
    allPages.forEach((Page) => {
      if (Page.dataset.active == 1) {
        window.scroll(
          0,
          Page.querySelector(".container").offsetTop +
            (Page.querySelector(".container").offsetHeight - 100)
        );
      }
    });
  }

  toUp() {
    const allItems = document.querySelectorAll(".navigate-item");
    const allPages = document.querySelectorAll(".page");
    const scrollBTN = document.querySelector("#scrollNextPage");
    if (allPages[0] !== undefined) {
      if (
        window.pageYOffset >=
        allPages[0].offsetTop + allPages[0].offsetHeight
      ) {
        scrollBTN.classList.remove("hiddenElem");
        scrollBTN.addEventListener("click", () => {
          if (this.state.scrollActivated === false) {
            this.setState({
              scrollActivated: !this.state.scrollActivated,
            });
          }
          this.scrollTo("#page-0");
          scrollBTN.classList.add("hiddenElem");
        });
      } else {
        scrollBTN.classList.add("hiddenElem");
      }
    }
  }

  scrollTo = (target) => {
    document.querySelector(target).scrollIntoView({
      behavior: "smooth",
      block: "start",
      inline: "center",
    });
  };

  StartCircularActions() {
    let menus = [...document.querySelectorAll(".circular-menu")];

    this.checkOffset();
    this.checkPosToScroll();
    window.addEventListener("scroll", () => {
      this.checkOffset();
    });

    const allItems = document.querySelectorAll(".navigate-item");
    const allPages = document.querySelectorAll(".page");
    allItems.forEach((Item) => {
      Item.addEventListener("click", () => {
        allPages.forEach((Page) => {
          if (Page.dataset.pageId == Item.dataset.pageId) {
            this.scrollTo("#" + Page.id);
            if (document.body.clientWidth <= 1250) {
              let NavBurger = document.querySelectorAll(".SmallDeviceBurger");
              let navigateBar = document.querySelector(".navigate");
              navigateBar.dataset.isopen = this.checkBool(
                navigateBar.dataset.isopen
              );
              NavBurger.forEach((Burger) => {
                Burger.dataset.isopen = this.checkBool(Burger.dataset.isopen);
              });
            }
          }
        });
      });
    });

    menus.map((menu) => {
      let items = menu.querySelectorAll(".circular-menu__item");
      let button = menu;

      let active = false;

      const length = items.length;
      const arc = 2 * Math.PI * (1 / length);
      const radius = 80;

      let langsArr = [];
      let changeLang = false;

      items.forEach((item) => {
        item.addEventListener("click", () => {
          items.forEach((itemInner) => {
            itemInner.dataset.active = 0;
          });
          item.dataset.active = 1;
          if (changeLang) {
            langsArr = [];
            items.forEach((itemInner) => {
              langsArr.push({
                id: itemInner.dataset.listId,
                name: itemInner.dataset.name,
                active: itemInner.dataset.active,
              });
            });

            localStorage.setItem("langActive", JSON.stringify(langsArr));
            this.setState({
              langs: JSON.parse(localStorage.getItem("langActive")),
            });
          } else {
            changeLang = !changeLang;
          }
        });
      });

      button.addEventListener("click", (e) => {
        e.preventDefault();
        active = !active;
        if (active) {
          button.classList.add("circular-menu__button_active");

          for (let i = 0; i < length; i++) {
            const angle = i * arc;
            const x = radius * Math.cos(angle);
            const y = radius * Math.sin(angle);

            items[i].classList.add("circular-menu__item_active");

            items[i].style.left = 10 - x + "%";
            items[i].style.top = 50 + y + "%";
          }
        } else {
          button.classList.remove("circular-menu__button_active");
          for (let i = 0; i < length; i++) {
            const angle = i * arc;
            const x = radius * Math.cos(angle);
            const y = radius * Math.sin(angle);
            items[i].classList.remove("circular-menu__item_active");
            items[i].removeAttribute("style");
          }
        }
      });
    });
  }
}

export default MainPage;
