import React, { Component } from "react";
import classNames from "classnames";
import Image from "../Image";

import "./style.min.css";

class Pages extends Component {
  render() {
    const { pages, lang } = this.props;
    const allPages = pages.map((page) => {
      let RenderTemplate;
      switch (lang) {
        case 1:
          RenderTemplate = page.templates.EN;
          break;
        default:
          RenderTemplate = page.templates.RU;
          break;
      }
      return (
        <div
          key={page.id}
          data-active={page.isActive}
          data-page-id={page.id}
          id={"page-" + page.id}
          className="container-wrapper page"
        >
          {RenderTemplate}
        </div>
      );
    });
    return allPages;
  }
}

export default Pages;
