import React, { Component } from "react";
import Image from "../Image";

import "./style.min.css";

export default class Registration extends Component {
  constructor (props) {
    super(props);
    this.state = {
      ActiveID: 2,
    };
    this.navigateBar = React.createRef();
    this.pages = React.createRef();
  }

  componentDidMount() {
    this.NavigateBar = this.navigateBar.current.querySelectorAll(".page-swicher__item");
    this.Pages = this.pages.current.querySelectorAll(".registration-page");
    this.checkOnActive();
    this.NavigateBar.forEach((item) => {
      item.addEventListener("click", () => {
        this.handleClick(item);
      })
    });
  }

  handleClick(item) {

    this.setState({
      ActiveID: item.dataset.pageId,
    });

    this.checkOnActive();
    
  }

  checkOnActive() {
    
    this.Pages.forEach((item) => {
      if (this.state.ActiveID == item.dataset.pageId) 
        item.dataset.active = "1";
      else item.dataset.active = "0";
    });
    this.NavigateBar.forEach((item) => {
      if (this.state.ActiveID == item.dataset.pageId) {
        item.dataset.active = "1";
      } else {
        item.dataset.active = "0";
      }
    })
  }

  render() {
    return (
      <div className="registration__wrapper">
        <div className="registration__inner-wrapper">
          <div className="registration-title__wrapper">
            <h2 className="registration__title">Уважаемый пользователь!</h2>
            <p className="registration__subtitle">
              Регистрация на платформе Empire Group Coin
            </p>
          </div>
          <div className="registration__page-switcher" ref={this.navigateBar}>
            <div
              data-page-id="0"
              data-active="0"
              className="page-swicher__item"
            >
              <div className="item-inner">Регистрация</div>
            </div>
            <div
              data-page-id="1"
              data-active="0"
              className="page-swicher__item"
            >
              <div className="item-inner">Забыли пароль?</div>
            </div>
            <div
              data-page-id="2"
              data-active="0"
              className="page-swicher__item"
            >
              <div className="item-inner">Вход</div>
            </div>
          </div>
          <div className="registration-form" ref={this.pages}>
            <div data-page-id="0" data-active="0" className="registration-page">
              <div className="registration-inner-page">
                <div className="registration-input__wrapper">
                  {
                    <Image
                      src={"../images/Name.svg"}
                      alt={"Пользователь Имя"}
                      className={"registration-image"}
                      width={"45"}
                      height={"45"}
                    />
                  }
                  <input type="text" />
                  <label htmlFor="regUsName">Ваше имя</label>
                </div>
                <div className="registration-input__wrapper">
                  {
                    <Image
                      src={"../images/Age.svg"}
                      alt={"Пользователь Возраст"}
                      className={"registration-image"}
                      width={"45"}
                      height={"45"}
                    />
                  }
                  <input type="text" />
                  <label htmlFor="regUsName">Ваш возраст</label>
                </div>
                <div className="registration-input__wrapper">
                  {
                    <Image
                      src={"../images/Email.svg"}
                      alt={"Пользователь Почта"}
                      className={"registration-image"}
                      width={"45"}
                      height={"45"}
                    />
                  }
                  <input type="text" />
                  <label htmlFor="regUsName">Ваш Email</label>
                </div>
                <div className="registration-input__wrapper">
                  {
                    <Image
                      src={"../images/Language.svg"}
                      alt={"Пользователь Язык"}
                      className={"registration-image"}
                      width={"45"}
                      height={"45"}
                    />
                  }
                  <input type="text" />
                  <label htmlFor="regUsName">Ваш язык</label>
                </div>
                <div className="registration-input__wrapper">
                  {
                    <Image
                      src={"../images/Country.svg"}
                      alt={"Пользователь Страна"}
                      className={"registration-image"}
                      width={"45"}
                      height={"45"}
                    />
                  }
                  <input type="text" />
                  <label htmlFor="regUsName">Ваша страна</label>
                </div>
                <div className="registration-input__wrapper">
                  {
                    <Image
                      src={"../images/Login.svg"}
                      alt={"Пользователь Логин"}
                      className={"registration-image"}
                      width={"45"}
                      height={"45"}
                    />
                  }
                  <input type="text" />
                  <label htmlFor="regUsName">Ваш логин</label>
                </div>
                <div className="registration__send-btn-wrapper">
                  <input
                    className="registration__send-btn"
                    value="Зарегистрироваться"
                    type="submit"
                  />
                </div>
              </div>
            </div>
            <div
              data-page-id="1"
              data-active="0"
              className="registration-page"
            >
              <div className="registration-inner-page">
                <div className="registration-input__wrapper">
                  {
                    <Image
                      src={"../images/Email.svg"}
                      alt={"Пользователь Почта"}
                      className={"registration-image"}
                      width={"45"}
                      height={"45"}
                    />
                  }
                  <input type="text" />
                  <label htmlFor="regUsName">Ваш Email</label>
                </div>
                <div className="registration__send-btn-wrapper-90">
                  <div className="registration__send-btn-wrapper">
                    <input
                      className="registration__send-btn"
                      value="Продолжить"
                      type="submit"
                    />
                  </div>
                </div> 
              </div>
            </div>
            <div
              data-page-id="2"
              data-active="0"
              className="registration-page"
            >
              <div className="registration-inner-page">
                <div className="registration-input__wrapper">
                  {
                    <Image
                      src={"../images/Email.svg"}
                      alt={"Пользователь Почта"}
                      className={"registration-image"}
                      width={"45"}
                      height={"45"}
                    />
                  }
                  <input type="text" />
                  <label htmlFor="regUsName">Ваш Email</label>
                </div>
                <div className="registration-input__wrapper">
                  {
                    <Image
                      src={"../images/Key.svg"}
                      alt={"Пользователь Почта"}
                      className={"registration-image"}
                      width={"45"}
                      height={"45"}
                    />
                  }
                  <input type="text" />
                  <label htmlFor="regUsName">Пароль</label>
                </div>
                <div className="registration__send-btn-wrapper">
                  <input
                    className="registration__send-btn"
                    value="Войти"
                    type="submit"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
