import React, { Component } from "react";
import Chart from "chart.js";

export default class Graphic extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentWillMount() {
    if (this.props.type == "line") {
      this.setState({
        canvasSize: {
          canvasWidth: 525,
          canvasHeight: 390,
        },
        data: {
          type: "line",
          temp: [
            { x: 0, y: 0 },
            { x: 0, y: 600000 },
            { x: 0, y: 5000000 },
            { x: 0, y: 13000000 },
            { x: 0, y: 14000000 },
          ],
          text: [
            [""],
            [
              `2020 год`,
              ` более 200 000`,
              ` уникальных`,
              ` просмотров в месяц `,
            ],
            [
              `2021 год`,
              ` более 700 000`,
              ` уникальных`,
              ` просмотров в месяц `,
            ],
            [
              `2022 год`,
              ` более 5 000 000`,
              ` уникальных`,
              ` просмотров в месяц `,
            ],
            [""],
          ],
        },
      });
    } else if (this.props.type == "tokenPage") {
      this.setState({
        canvasSize: {
          canvasWidth: 320,
          canvasHeight: 320,
        },
        data: {
          type: "doughnut",
          temp: [10, 10, 15, 65],
          text: [
            "10% Команда платформы",
            "10% Амбассадоры платформы",
            "15% Резервный фонд",
            "65% Участники платформы",
          ],
        },
      });
    }
  }

  componentDidMount() {
    const { canvasWidth, canvasHeight } = this.state.canvasSize;
    this.canvasHex.width = canvasWidth;
    this.canvasHex.height = canvasHeight;

    let ctx = this.canvasHex.getContext("2d");
    let gradientStroke = ctx.createLinearGradient(320, 0, 0, 320);
    gradientStroke.addColorStop(0, "#008bb7");
    gradientStroke.addColorStop(0.9, "#e4c13b");
    gradientStroke.addColorStop(1, "#e4c13b");

    let i = 0;
    if (this.state.data.type == "line") {
      let myChart = new Chart(ctx, {
        type: this.state.data.type,
        data: {
          labels: this.state.data.text,
          datasets: [
            {
              data: this.state.data.temp,
              pointRadius: 4,
              backgroundColor: "rgba(250,250,250,0)",
              pointBackgroundColor: gradientStroke,
              borderColor: gradientStroke,
              lineTension: 0,
              borderWidth: 5,
            },
          ],
        },
        options: {
          scales: {
            yAxes: [
              {
                position: "right",
                ticks: {
                  display: false,
                },

                gridLines: {
                  display: true,
                  color: "rgba(60,60,60,1)",
                  lineWidth: 2,
                  borderDash: [10, 5],
                },
                offset: 0,
                scaleLabel: 12,
              },
            ],
            xAxes: [
              {
                ticks: {
                  display: true,
                  fontColor: "#fff",
                  fontSize: 11,
                  lineHeight: 1.4,
                  maxRotation: 0,
                },
                gridLines: {
                  display: true,
                  color: "rgba(60,60,60,1)",
                  lineWidth: 2,
                  borderDash: [10, 5],
                },
                offset: 0,
                scaleLabel: 0,
              },
            ],
          },
          legend: {
            display: false,
          },
          tooltips: {
            enabled: false,
          },
          layout: {
            padding: {
              top: 15,
              left: 15,
            },
          },
        },
      });
    } else if (this.state.data.type == "doughnut") {
      let myChart = new Chart(ctx, {
        type: this.state.data.type,
        data: {
          labels: "",
          datasets: [
            {
              data: this.state.data.temp,
              backgroundColor: [
                "#EFE054",
                "#01DAD2",
                "#00807D",
                gradientStroke,
              ],
              pointBackgroundColor: "rgba(250,250,250,.5)",
              borderColor: "rgba(0,0,0,0)",
              borderWidth: 1,
            },
          ],
        },
        options: {
          scales: {
            yAxes: [
              {
                display: false,
                ticks: {
                  display: false,
                },

                gridLines: {
                  display: false,
                },
              },
            ],
            xAxes: [
              {
                display: false,
                ticks: {
                  display: false,
                },
                gridLines: {
                  display: false,
                },
              },
            ],
          },
          legend: {
            display: false,
          },
          tooltips: {
            enabled: false,
          },
        },
      });
    }
  }

  render() {
    return (
      <canvas
        id="graph"
        className="dynamic-graph"
        className={this.props.className}
        ref={(canvasHex) => (this.canvasHex = canvasHex)}
      ></canvas>
    );
  }
}
