import React, { Component } from "react";
import Graphic from "../../components/Graphical";
import Image from "../Image";

export default class TokenInfo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isOpen: "false",
    };
    this.coinInfo = this.props.coinInfo;
  }

  handleClick() {
    this.setState({
      isOpen: this.checkBool(this.state.isOpen),
    });
  }

  checkBool(elem) {
    switch (elem) {
      case "false":
        return "true";
        break;
      default:
        return "false";

    }
  }

  render() {
    const Body = (
      <div className="title">
        <div className="container-token">
          <div className="title-text-small title-text">Информация о токене</div>
          <div className="subtitle">
            <div className="toggle-event-wrapper">
              <div className="toggle-event">
                <div className="toggle-item" data-active={this.state.isOpen == "false" ? "0" : "1"} onClick={()=>{this.handleClick()}}>
                  <div className="toggle-text">Информация</div>
                </div>
                <div className="toggle-item" data-active={this.state.isOpen == "false" ? "1" : "0"} onClick={()=>{this.handleClick()}}>
                  <div className="toggle-text">Распределение</div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="container-token-content" data-active={this.state.isOpen == "false" ? "1" : "0"}>
          <div className="container-block__token-graph-wrpaper">
            <div className="container-block__token-graph-wrpaper-inner">
              <div className="doughnut-outwrapper">
                <div className="doughnut-wrapper">
                  <Graphic className="doughnut" type="tokenPage" />
                </div>
                <p className="item-1">
                  <span className="item-percent">10 %</span>
                  <span className="item-text">Команда платформы</span>
                </p>
                <p className="item-2">
                  <span className="item-percent">10 %</span>
                  <span className="item-text">Амбассадоры платформы</span>
                </p>
                <p className="item-3">
                  <span className="item-percent">15 %</span>
                  <span className="item-text">
                    Резервный <br /> фонд
                  </span>
                </p>
                <p className="item-4">
                  <span className="item-percent-last">65 %</span>
                  <span className="item-text">Участники платформы</span>
                </p>
              </div>
            </div>
          </div>
          <ul className="container-content container-list info__page-list fz-sm">
            <li className="container-item">
              <div className="container-item__circle"></div>
              <b>Участники платформы</b> — начисление токенов ERGC <br />
              клиентам платфомы, за Staking(Замарозка токенов по средством стэйкинга), за выполнение заданий размещенных{" "}
              <br />
              в телеграм ботах и другие действия, часть токенов может остаться у{" "}
              <br />
              платфомы и использоваться в дальнейшем для поддержания <br />
              работы проекта.
            </li>
            <li className="container-item">
              <div className="container-item__circle"></div>
              <b>Команда проекта</b> — основатели, входящие в команду, <br />и
              команда проекта. Токены, которые останутся у команды (10%), <br />
              будут заморожены и станут доступны для передачи, <br />
              использования и продажи через 365 дней.
            </li>
            <li className="container-item">
              <div className="container-item__circle"></div>
              <b>Амбассадоры бренда</b> — консультанты и лица, оказывающие{" "}
              <br />
              поддержку на раннем этапе развития проекта, ключевые партнеры{" "}
              <br />в блокчейн-программе с максимальными статусами на протяжении{" "}
              <br />
              старта проекта.
            </li>
            <li className="container-item">
              <div className="container-item__circle"></div>
              <b>Резервный фонд</b> — фонд проекта, для решения ситуации
              форс-мажор.
            </li>
            <li className="container-item">
              <div id="startMember" className="title-button__reg">
                <div className="helpselems">Регистрация</div>
              </div>
            </li>
          </ul>
        </div>
        <div className="container-token-content" data-active={this.state.isOpen == "false" ? "0" : "1"}>
          <div className="container-block__logotype-wrapper">
            {
              <Image
                src={"./images/logoLarge.png"}
                alt={"Логотип большой"}
                className={"logotype"}
                width={"300"}
                height={"300"}
              />
            }
          </div>
          <div className="token-info">
            <div className="token-info__row">
              <div className="token-info__pretext">Название</div>
              <div className="token-info__text">{this.coinInfo.TokenName}</div>
            </div>
            <div className="token-info__row">
              <div className="token-info__pretext">Торгвое название</div>
              <div className="token-info__text">{this.coinInfo.CurrName}</div>
            </div>
            <div className="token-info__row">
              <div className="token-info__pretext">Объем эмиссии</div>
              <div className="token-info__text">{this.coinInfo.EmissionVolume}</div>
            </div>
            <div className="token-info__row">
              <div className="token-info__pretext">Алгоритм</div>
              <div className="token-info__text">{this.coinInfo.Algorithm}</div>
            </div>
            <div className="token-info__row">
              <div className="token-info__pretext">Цена монеты</div>
              <div className="token-info__text">{this.coinInfo.Price}$</div>
            </div>
            <div className="token-info__row">Токен создан на Blockchain TRON</div>
          </div>
        </div>
      </div>
    );
    return Body;
  }
}
