import React from "react";
import {
  BrowserRouter,
  Route, 
  NavLink
} from "react-router-dom";

import MainPage from "../MainPage";
import Registration from "../Registration";

const App = (props) => {
	let Routes = (
		<BrowserRouter>
			<Route exact path="/">
		      <MainPage />
		    </Route>
		    <Route path="/account/registration"> 
		      <Registration />
		    </Route>
		</BrowserRouter>
		)
    return Routes
}

export default App;
