import React, { Component } from "react";
import classNames from "classnames";

import Image from "../Image";
import Langs from "../Langs";

import Pages from "../Pages";
import Asidenavbar from "../Asidenavbar";
import SliderSlick from "../Slider";

import "./style.min.css";

class Content extends Component {
  render() {
    let pages;
    switch (this.props.lang) {
      case 1:
        pages = <Pages pages={this.props.pages} lang={1} />;
        break;
      default:
        pages = <Pages pages={this.props.pages} lang={0} />;
        break;
    }
    return (
      <div className="content-wrapper">
        <div className="pages-wrapper">{pages}</div>
        <Asidenavbar allpages={this.props.pages} lang={this.props.lang} />
      </div>
    );
  }
}

export default Content;
