import React, { Component } from "react";

export default class Langs extends Component {
  state = {
    Langs: this.props.langs,
  };
  render() {
    const AllLangs = this.state.Langs.map((lang) => (
      <li
        key={lang.id}
        className="circular-menu__item"
        data-active={lang.active}
        data-list-id={lang.id}
        data-list-name={lang.name}
      >
        {lang.name}
      </li>
    ));
    return AllLangs;
  }
}
