import React, { Component } from "react";
import Image from "../Image";

export default class Article extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isOpen: "false",
    }
  }

  handleClick() {
    this.setState({
      isOpen: this.checkBool(this.state.isOpen)
    })
  }

  checkBool(elem) {
    switch (elem) {
      case "false":
        return "true";
        break;
      default:
        return "false";

    }
  }

  render() {
    const {content} = this.props;
    const articleTitle = (
      <div className="article-title-text">{content.title}</div>
    );
    const articleBody = <div className="article-text">{content.content}</div>;
    const Article = (
      <div
        className="article"
        data-active={this.state.isOpen == "false" ? "false" : "true"}
      >
        <h3 className="article-title">
          {articleTitle}
          <button onClick={()=>{this.handleClick()}} className="article-open-button">
            {document.body.clientWidth <= 768 ? "" : this.state.isOpen == "false" ? "Показать" : "Скрыть"}
            {
              <Image
                src={"./images/arrow.svg"}
                alt={"Стрелка"}
                className={"article-arrow"}
                width={"400"}
                height={"400"}
              />
            }
          </button>
        </h3>
        {articleBody}
      </div>
    );
    return Article;
  }
}
