import React, { Component } from "react";
import classNames from "classnames";

import "./style.min.css";

export default class Soon extends Component {
  constructor() {
    super();
    this.state = {
      hourActive: 0,
    };
    this.state.hours = this.createHours();
  }

  componentDidMount() {
    this.Clock();
  }

  createHours() {
    let arr = [];
    for (let i = 0; i < 12; i++) {
      arr.push(
        <div key={i} className="hour" data-id={i} id={"hour-" + i}></div>
      );
    }
    return arr;
  }

  render() {
    let hours = this.state.hours;
    return (
      <div className="clock-soon-wrapper">
        <div className="Clock">
          <div id="Clock-soon">{hours}Скоро...</div>
        </div>
      </div>
    );
  }

  Clock() {
    let i = -3;
    let degree = 0;
    document.querySelectorAll(".hour").forEach((hour) => {
      const length = document.querySelectorAll(".hour").length;
      const arc = 2 * Math.PI * (1 / length);
      const radius =
        document.body.clientWidth <= 768
          ? document.body.clientWidth / 4
          : document.body.clientWidth / 8;

      const angle = i * arc;
      const x = radius * Math.cos(angle);
      const y = radius * Math.sin(angle);
      hour.style.position = "absolute";
      hour.style.transform = "rotateZ(" + degree + "deg)";
      degree += 30;
      hour.style.left = 10 + x + "px";
      hour.style.top = 10 + y + "px";
      i++;
    });

    this.startTick();
  }

  startTick() {
    if (document.querySelectorAll(".hour").length > this.ClockInterval) {
      document.querySelectorAll(".hour").forEach((item) => {
        item.classList.remove("hour-active");
      });
      document
        .querySelectorAll(".hour")
        [this.ClockInterval].classList.add("hour-active");

      this.ClockInterval++;
    } else {
      this.ClockInterval = 0;
    }
    let tick = setInterval(this.startTick, 100);
  }
}
